.photo-card {
    position: relative;
    border-radius: var(--border-radius);
    overflow: hidden;
    transition: var(--transition);
  }
  
  .photo-card:hover {
    transform: scale(1.02);
    box-shadow: var(--shadow-md);
  }
  
  .photo-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .photo-actions {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    opacity: 0;
    transition: var(--transition);
  }
  
  .photo-card:hover .photo-actions {
    opacity: 1;
  }
  
  .photo-action-btn {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--primary);
    color: var(--white);
    border-radius: 50%;
    border: none;
    cursor: pointer;
  }
  
  .photo-meta {
    padding: 1rem;
    background-color: var(--white);
  }
  
  .photo-author {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    margin-bottom: 0.5rem;
  }
  
  .photo-author-avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .photo-stats {
    display: flex;
    gap: 1rem;
    font-size: 0.875rem;
    color: var(--text-gray);
  }
  
  .photo-stat {
    display: flex;
    align-items: center;
    gap: 0.25rem;
  }